import { useState, useEffect } from 'react';
import articleContet from './article-content';
import ArticleList from '../components/ArticlesList';
import NotFoundPage from '../pages/NotFoundPage';
import CommentsList from '../components/CommentsList';
import UpvotesSection from '../components/UpvotesSection';
import AddCommentForm from '../components/AddCommentFrom';

function ArticlePage({ match }) {
    const name = match.params.name;
    const article = articleContet.find(article => article.name === name)
    const [articleInfo, setArticleInfo] = useState({ upvotes: 0, comments: [] });
    const otherArticles = articleContet.filter(article => article.name !== name);
    useEffect(() => {

        const fecthData = async () => {
            console.log("hola")
            let result = await fetch(`/api/articles/${name}/`);
            console.log('response data?', result);
            const body = await result.json();
            console.log(body);
            setArticleInfo(body);
        }
        fecthData();

    }, [name]);
    if (!article) return <NotFoundPage />
    return (
        <div>
            <h1>{article.title}!</h1>
            <UpvotesSection articleName={name} upvotes={articleInfo.upvotes} setArticleInfo={setArticleInfo} />
            {article.content.map((paragraph, key) =>
                <p key={key}>{paragraph}</p>)}
            <CommentsList comments={articleInfo.comments} />
            <AddCommentForm articleName={name} setArticleInfo={setArticleInfo} />
            <h2>Other Articles:</h2>
            <ArticleList articles={otherArticles} />
        </div>
    )
}
export default ArticlePage;